* {
    font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans,
        Helvetica Neue, sans-serifbody, html;
}

html {
    height: 100%;
}

img {
    border-style: none;
}

body {
    overflow-y: hidden;
    height: 100%;
    margin: 0;
    padding: 0;
}

a {
    position: relative;
    text-decoration: none;
    margin: 5px;
    -ms-flex-item-align: end;
        align-self: flex-end;
    text-transform: uppercase;
    font-size: 10px;
    font-weight: 500;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    opacity: 0.45;
    color: black;
}

a:hover {
    opacity: 1;
}

a:before {
    content: '';
    position: absolute;
    width: 100%;
    height: 1px;
    bottom: -5px;
    left: 0;
    background-color: black;
    visibility: hidden;
    -webkit-transform: scaleX(0);
        -ms-transform: scaleX(0);
            transform: scaleX(0);
    -webkit-transition: all 0.25s ease-in-out;
    -o-transition: all 0.25s ease-in-out;
    transition: all 0.25s ease-in-out;
}

a:hover:before {
    visibility: visible;
    -webkit-transform: scaleX(1);
        -ms-transform: scaleX(1);
            transform: scaleX(1);
}

#root {
    height: 100%;
}

.Scene {
    position: relative;
    height: 100%;
    width: 100%;
    max-width: 500px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    margin: 0 auto;
    overflow: hidden;
}

.Scene:before {
    content: '';
    float: left;
    padding-top: 100%;
}

.Scene-content {
    position: relative;
}

.Winning-screen {
    position: relative;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    -webkit-transition: opacity 0.8s ease-out;
    -o-transition: opacity 0.8s ease-out;
    transition: opacity 0.8s ease-out;
    opacity: 0;
    max-height: 530px;
    height: calc(100vw + 30px);
}

.Full-image {
    width: 100%;
    position: relative;
    display: block;
}

.Origin-link {
    -ms-flex-item-align: end;
        align-self: flex-end;
}

.Sub-section {
    position: relative;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
}

.Tutorial {
    -webkit-transition: opacity 0.8s ease-out;
    -o-transition: opacity 0.8s ease-out;
    transition: opacity 0.8s ease-out;
    opacity: 1;
}

.New-game-button {
    color: #fff;
    background: #000;
    display: inline-block;
    width: 200px;
    height: 50px;
    border: 2px solid #000;
    font-size: 12px;
    text-transform: uppercase;
    cursor: pointer;
    text-align: center;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    position: relative;
    overflow: hidden;
    margin: 20px;
    -webkit-transition: border 0.2s, background 0.2s, color 0.2s ease-out;
    -o-transition: border 0.2s, background 0.2s, color 0.2s ease-out;
    transition: border 0.2s, background 0.2s, color 0.2s ease-out;
}

.New-game-button:hover {
    color: rgb(0, 0, 0);
    border-width: 2px;
    border-style: solid;
    border-color: rgb(0, 0, 0);
    -webkit-border-image: initial;
         -o-border-image: initial;
            border-image: initial;
    background: transparent;
}

.About {
    position: absolute;
    bottom: 10px;
    left: calc(50% - 22px);
}



